.timeouter {
    /*display: inline-block;*/
    display: flex;
    text-align: center;
    
    background-color: #050c1b;
    width : 100%;
    /* height: 90vh; */
    height: 100vh;
}

.timeinner {
    /*  */
    position: absolute;
    transform: translate(-50%, -50%);
    
    text-align: left;

    background-color: #050c1b;
    color: white;
    top: 50%;
    left: 50%;
    width: 350px;
    height: 50px;
    
    font-size: 35px;
}

.timecss {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(15px + 2vmin);
    color: white;
}